export default {
	data() {
		return {
			tempThumbPath: [],
			tempVideoPath: [],
			tenVideos: [],
			result: {},
			src: "",
			alpha: "",
			b: "",
			c: "",
			disabled: false,
			ctx: {}
		}
	},
	onLoad() {
		var that = this
		that.ctx = uni.createCameraContext()
	},
	methods: {
		yqz2: function() {
			var that = this
			uni.showToast({
				title: "模拟考试录制开始",
				icon: "none"
			})
			that.ctx.startRecord({
				success: (res) => {
					console.log(res)
					for (let count = 0; count < 10; count++) {
						(function(num) {
							setTimeout(function() {
								uni.showToast({
									title: "请看着屏幕" + num,
									icon: "none"
								})
								setTimeout(function() {
									that.ctx.stopRecord({
										success: (res) => {
											console.log(res)
											that.tenVideos[num] = res.tempVideoPath
											if (num < 9) {
												that.ctx.startRecord({
													success: (res) => {
														console.log(res)
													}
												})
											} else {
												uni.showToast({
													title: "第二段录制结束",
													icon: "none"
												})
												var pages = getCurrentPages();
												if (pages.length > 1) {
													//上一个页面实例对象
													var prePage = pages[pages.length - 2];
													//关键在这里
													prePage.$vm.set_tempVideoPath(JSON.stringify(that.tempVideoPath))
													prePage.$vm.set_tenVideos(JSON.stringify(that.tenVideos))
													setTimeout(function() {
														uni.navigateBack({
															delta: 1
														})
													}, 2000)
												}
											}
										},
										fail: (e) => {
											console.log(e)
										}
									})
								}, 28000)
							}, num * 31000)
						})(count)
					}
				}
			})
		},
		yqz: function() {
			var that = this
			uni.showToast({
				title: "录制开始",
				icon: "none"
			})
			that.ctx.startRecord({
				success: (res) => {
					console.log(res)
					for (let count = 0; count < 5; count++) {
						(function(num) {
							setTimeout(function() {
								console.log(num)
								if (num == 0) {
									uni.showToast({
										title: "请点头",
										icon: "none"
									})
									setTimeout(function() {
										uni.showToast({
											title: "再点头",
											icon: "none"
										})
									}, 5000)
								} else if (num == 1) {
									uni.showToast({
										title: "请张嘴",
										icon: "none"
									})
									setTimeout(function() {
										uni.showToast({
											title: "再张嘴",
											icon: "none"
										})
									}, 5000)
								} else if (num == 2) {
									uni.showToast({
										title: "请一直眨眼",
										icon: "none"
									})
									setTimeout(function() {
										uni.showToast({
											title: "再一直眨眼",
											icon: "none"
										})
									}, 5000)
								} else if (num == 3) {
									uni.showToast({
										title: "请摇头",
										icon: "none"
									})
									setTimeout(function() {
										uni.showToast({
											title: "再摇头",
											icon: "none"
										})
									}, 5000)
								} else if (num == 4) {
									uni.showToast({
										title: "请保持不动",
										icon: "none"
									})
									setTimeout(function() {
										uni.showToast({
											title: "请保持不动",
											icon: "none"
										})
									}, 5000)
								}
								setTimeout(function() {
									that.ctx.stopRecord({
										success: (res) => {
											console.log(res)
											that.tempThumbPath[num] = res.tempThumbPath
											that.tempVideoPath[num] = res.tempVideoPath
											if (num < 4) {
												that.ctx.startRecord({
													success: (res) => {
														console.log(res)
													}
												})
											} else {
												console.log(num)
												uni.showToast({
													title: "第一段录制结束",
													icon: "none"
												})
												that.yqz2()
											}
										},
										fail: (e) => {
											console.log(e)
										}
									})
								}, 11000)
							}, num * 13000)
						})(count)
					}
				}
			})
		},
		error(e) {
			console.log(e.detail)
		}
	}
}
